import AgreementService from '@/services/sweep/AgreementService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let AgreementTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultAgreementTableFilter: {
      type: String,
      default: null
    },
    defaultAgreementTableOrderBy: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      agreementTableData: [],
      agreementTableFilterTimer: null,
      agreementTableFilter: this.defaultAgreementTableFilter,
      agreementTableOrderBy: this.defaultAgreementTableOrderBy,
      agreementTableContainer: 'loading-container',
      agreementTableBusy: false,
      agreementTableTotalRows: 0,
      agreementTableCurrentPage: 1,
      agreementTablePerPage: 10
    }
  },
  computed: {
    agreementTableNoDataText: function () {
      return this.agreementTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    agreementTableTotalPages: function () {
      return Math.ceil(this.agreementTableTotalRows / this.agreementTablePerPage)
    },
    agreementTablePagingInfo: function () {
      return this.pagingInfo(this.agreementTableCurrentPage, this.agreementTablePerPage, this.agreementTableTotalRows)
    }
  },
  methods: {
    loadAgreementTableData (loadingContainerId) {
      let self = this
      this.agreementTableBusy = true
      this.agreementTableContainer = loadingContainerId || this.agreementTableContainer
      this.startLoading(this.agreementTableContainer)
      AgreementService.agreementes({
        pageIndex: this.agreementTableCurrentPage - 1,
        pageSize: this.agreementTablePerPage,
        filter: this.agreementTableFilter,
        orderByField: this.agreementTableOrderBy
      }).then(function (response) {
        self.stopLoading(self.agreementTableContainer)
        self.agreementTableBusy = false
        self.agreementTableTotalRows = response.data.totalRows
        self.agreementTableData = response.data.agreementList || []
      }).catch(function (error) {
        self.stopLoading(self.agreementTableContainer)
        self.agreementTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshAgreementTable () {
      this.loadAgreementTableData()
    },
    filterAgreementTable (filter) {
      let self = this
      if (this.agreementTableFilter !== filter) {
        clearTimeout(this.agreementTableFilterTimer)
        this.agreementTableFilterTimer = setTimeout(() => {
          self.agreementTableFilter = filter
          self.loadAgreementTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.agreementTableFilterTimer)
      }
    },
    orderAgreementTable (orderBy, active) {
      this.agreementTableOrderBy = (active ? '' : '-') + orderBy
      this.loadAgreementTableData()
    }
  },
  watch: {
    agreementTableCurrentPage: function () {
      this.refreshAgreementTable()
    },
    agreementTablePerPage: function () {
      this.refreshAgreementTable()
    }
  }
}

export default AgreementTableMixin
