import Endpoint from '@/services/Endpoint'

export default {
  agreements (payload) {
    return Endpoint().get('/s1/sweep/agreement', {
      params: payload
    })
  },
  secondaryAccounts (payload) {
    return Endpoint().get(`/s1/sweep/agreement/${payload.agreementId}/secondaryAccount`, {
      params: payload
    })
  },
  runs (payload) {
    return Endpoint().get(`/s1/sweep/agreement/${payload.agreementId}/run`, {
      params: payload
    })
  }
}
